export class Option {
    public label!: string;

    constructor(label: string = '') {
        this.label = label;
    }
}

export enum RSVP {
    ID = 'id',
    EMAIL = 'email',
    NAME = 'full_name',
    ATTENDING = 'can_attend',
    HAS_GUEST = 'has_guest',
    GUEST_NAME = 'guest_name',
    EXTRA = 'extra_info',
    SONGS = 'suggested_songs',
    CREATED_AT = 'created_at',
    UPDATED_AT = 'updated_at',
}

export interface RsvpRecord {
    [RSVP.ID]: number;
    [RSVP.EMAIL]: string;
    [RSVP.NAME]: string;
    [RSVP.ATTENDING]: boolean;
    [RSVP.HAS_GUEST]: boolean;
    [RSVP.GUEST_NAME]: string;
    [RSVP.EXTRA]: string;
    [RSVP.SONGS]: string;
    [RSVP.CREATED_AT]: string;
    [RSVP.UPDATED_AT]: string;
}

export enum DIRECTION {
    UP,
    DOWN,
}

/* tslint:disable:max-classes-per-file */
export class SortOption {
    public column!: RSVP;
    public direction!: DIRECTION;

    constructor(column: RSVP, direction: DIRECTION) {
        this.column = column;
        this.direction = direction;
    }
}
