


















































import {Component, Vue, Watch} from 'vue-property-decorator';
import NavItem from '@/components/NavItem.vue';
import Divider from '@/components/Divider.vue';
import MobileNavItem from '@/components/MobileNavItem.vue';
import InputField from '@/components/InputField.vue';
import SubmitButton from '@/components/SubmitButton.vue';

@Component({
    components: {
        SubmitButton,
        InputField,
        MobileNavItem,
        Divider,
        NavItem,
    },
})
export default class App extends Vue {
    public anchorToHighlight: string = '';
    private isSticky: boolean = false;
    private mobileNavVisible: boolean = false;
    private passcode: string = '';
    private passcodeError: string = '';
    private loggedOut: boolean = true;
    private guestPasscode: string = 'katdanny2021';
    private adminPasscode: string = 'dk';

    public created() {
        window.addEventListener('scroll', this.handleScroll);
        this.setScrollBasedOnLogin();
    }

    public destroyed() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    public handleScroll() {
        const currentScrollPosition = document.documentElement.scrollTop;
        const maxScrollPosition = document.getElementById('header')!.getBoundingClientRect().height - 86;
        this.isSticky = currentScrollPosition >= maxScrollPosition;
    }

    public toggleMobileNav() {
        this.mobileNavVisible = !this.mobileNavVisible;
    }

    public submit() {
        this.passcodeError = '';
        const expectedCode = this.$route.name === 'RSVP List' ? this.adminPasscode : this.guestPasscode;
        if (this.passcode.length > 0 && this.passcode.toLowerCase().replace(/\s/g, '') === expectedCode) {
            this.loggedOut = false;
        } else {
            this.passcodeError = 'Wrong passcode entered. Check your invitation for the passcode.';
        }
    }

    public submitIfEnter(e) {
      if (e.key === 'Enter') {
        this.submit();
      }
    }

    public setScrollBasedOnLogin() {
        if (this.loggedOut) {
            document.documentElement.style.overflow = 'hidden';
        } else {
            document.documentElement.style.overflow = 'auto';
        }
    }

    @Watch('loggedOut')
    public onLoggedOutChanged() {
        this.setScrollBasedOnLogin();
    }

    @Watch('$route')
    public routeChanged(route) {
        if (route.hash) {
          this.anchorToHighlight = route.hash;
        } else {
          this.anchorToHighlight = '';
        }
    }
}
