


















































import {Component, Vue, Watch} from 'vue-property-decorator';
import Card from '@/components/Card.vue';
import InputField from '@/components/InputField.vue';
import RadioButton from '@/components/RadioButton.vue';
import {Option, RsvpRecord} from '@/helpers/Classes';
import InputTextarea from '@/components/InputTextarea.vue';
import SubmitButton from '@/components/SubmitButton.vue';
import CardWithFlorals from '@/components/CardWithFlorals.vue';

@Component({
    components: {
        CardWithFlorals,
        SubmitButton,
        InputTextarea,
        RadioButton,
        Card,
        InputField,
    },
})

export default class RSVP extends Vue {
    public fullName: string = '';
    public email: string = '';
    public guestName: string = '';
    public suggestedSongs: string = '';
    public extraInfo: string = '';

    public canAttendOption = new Option('I\'ll be there!');
    public cannotAttendOption = new Option('Sad to miss it :(');
    public attendanceSelection: Option = this.canAttendOption;
    public attendanceOptions: [Option, Option] = [this.canAttendOption, this.cannotAttendOption];

    public bringingGuestOption = new Option('Yes');
    public notBringingGuestOption = new Option('No');
    public guestSelection: Option = this.bringingGuestOption;
    public guestOptions: [Option, Option] = [this.bringingGuestOption, this.notBringingGuestOption];

    public existingRecord: RsvpRecord = null;
    public loading: boolean = false;
    public showErrors: boolean = false;
    public submitted: boolean = false;

    public showSubmissionError: boolean = false;
    private emailTo: string = 'kataleksandrova@gmail.com,dannyhweinberg@gmail.com';
    private emailSubject: string = encodeURIComponent('RSVP to Kat and Danny\'s Wedding');
    private emailBody: string = '';

    get canAttend() {
        return this.attendanceSelection.label === this.canAttendOption.label;
    }

    get hasGuest() {
        return this.guestSelection.label === this.bringingGuestOption.label;
    }

    get nameError() {
        if (this.showErrors && this.fullName === '') {
            return 'Your full name is required.';
        }
        return '';
    }

    get emailError() {
        if (this.showErrors && this.email === '') {
            return 'Your email is required.';
        }
        return '';
    }

    get guestError() {
        if (this.showErrors && this.guestName === '') {
            return 'Your guest\'s full name is required.';
        }
        return '';
    }

    get emailClientLink() {
        return `mailto:${this.emailTo}?subject=${this.emailSubject}&body=${this.emailBody}`;
    }

    get gmailLink() {
        return `https://mail.google.com/mail/?view=cm&fs=1&to=${
            this.emailTo}&su=${this.emailSubject}&body=${this.emailBody}`;
    }

    public mounted() {
        fetch('//datwedding2020.herokuapp.com/');
    }

    public fetchRecord() {
        fetch(`//datwedding2020.herokuapp.com/rsvps/${this.email}`).then((response) => {
            if (response.ok) {
                response.clone().json().then((data) => {
                    this.existingRecord = data.rsvp as RsvpRecord;
                });
            } else {
                this.existingRecord = null;
            }
        });
    }

    public submit() {
        this.showErrors = false;
        this.showSubmissionError = false;
        this.submitted = false;
        if (!this.fullName || !this.email) {
            this.showErrors = true;
            return;
        }

        const payload: { [key: string]: any; } = {
            full_name: this.fullName,
            email: this.email,
            can_attend: this.canAttend,
            extra_info: this.extraInfo,
        };
        if (this.canAttend) {
            payload.has_guest = this.hasGuest;
            if (this.hasGuest) {
                if (!this.guestName) {
                    this.showErrors = true;
                    return;
                }
                payload.guest_name = this.guestName;
            }
            payload.suggested_songs = this.suggestedSongs;
        }
        this.loading = true;
        fetch(
            '//datwedding2020.herokuapp.com/rsvps', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(payload),
            },
        ).then((response) => {
            setTimeout(() => {
                if (response.ok) {
                    this.submitted = true;
                } else {
                    this.showSubmissionError = true;
                    this.setEmailBody();
                }
                this.loading = false;
            }, 1500);
        }).catch(() => {
            this.showSubmissionError = true;
            this.setEmailBody();
            this.loading = false;
        });
    }

    public setEmailBody() {
        this.emailBody = encodeURIComponent([
            `Full Name: ${this.fullName}`,
            `Email: ${this.email}`,
            `Attending: ${this.canAttend ? 'Yes' : 'No'}`,
            `Bringing Guest: ${this.hasGuest ? `Yes\nGuest's Name: ${this.guestName}` : 'No'}`,
            `Suggested Songs:\n${this.suggestedSongs}\n`,
            `Extra Info:\n${this.extraInfo}`,
        ].join('\n'));
    }

    @Watch('existingRecord', {deep: true})
    public onPropertyChanged(record: RsvpRecord) {
        if (record.email !== undefined) {
            this.fullName = record.full_name;
            this.email = record.email;
            this.attendanceSelection = record.can_attend ? this.canAttendOption : this.cannotAttendOption;
            this.guestSelection = record.has_guest ? this.bringingGuestOption : this.notBringingGuestOption;
            this.guestName = record.guest_name;
            this.extraInfo = record.extra_info;
            this.suggestedSongs = record.suggested_songs;
        }
    }
}
