













import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import {Option} from '@/helpers/Classes';

@Component
export default class RadioButton extends Vue {
    @Prop() public value!: Option;
    @Prop() public options!: [Option];
    @Prop() public label!: string;

    public fieldValue: Option = new Option();

    public mounted() {
        this.fieldValue = this.value;
    }

    public setSelectedOption(option: Option) {
        this.$emit('input', option);
    }

    public checkSelected(option: Option) {
        if (!this.fieldValue) {
            return false;
        }
        return this.fieldValue.label === option.label;
    }

    @Watch('fieldValue')
    public onFieldValueChanged() {
        this.$emit('input', this.fieldValue);
    }

    @Watch('value')
    public onValueChanged(value: Option) {
        this.fieldValue = value;
    }
}
