











































import {Component, Vue} from 'vue-property-decorator';
import DatTitle from '@/components/DatTitle.vue';
import {DIRECTION, RSVP, RsvpRecord, SortOption} from '@/helpers/Classes';

@Component({
  components: {DatTitle},
})

export default class RSVPList extends Vue {
  public rsvps: RsvpRecord[] = [];
  public showError: boolean = false;
  public RSVP = RSVP;
  public isLoading: boolean = true;
  private currentSort: SortOption = new SortOption(RSVP.ID, DIRECTION.DOWN);

  public mounted() {
    fetch('//datwedding2020.herokuapp.com/rsvps').then((response) => {
      response.json().then((list) => {
        this.rsvps = (list.rsvps as RsvpRecord[]).filter((row) => {
          // filter out testing data
          return row[RSVP.ID] > 5;
        });
        this.isLoading = false;
      });
    }).catch(() => {
      this.showError = true;
      this.isLoading = true;
    });
  }

  public sort(column: RSVP) {
    if (this.currentSort.column === column) {
      // change direction of existing column
      this.currentSort.direction = this.currentSort.direction === DIRECTION.DOWN ? DIRECTION.UP : DIRECTION.DOWN;
    } else {
      this.currentSort.column = column;
      this.currentSort.direction = DIRECTION.DOWN;
    }

    // sort data and reflect it in the UI
    this.rsvps = this.rsvps.sort((a, b) => {
      let aValue: any = a[this.currentSort.column];
      let bValue: any = b[this.currentSort.column];

      // when sorting on a nullable column, always keep null rows on the bottom regardless of direction.
      if (aValue !== null && bValue === null) {
        return -1;
      }
      if (aValue === null && bValue !== null) {
        return 1;
      }

      // handle dates and strings
      if (typeof aValue === 'string' && typeof bValue === 'string') {
        if (!isNaN(new Date(aValue).getTime())) {
          // if the above doesn't equate to NaN, these are dates so we convert them to date objects for comparison
          aValue = new Date(aValue);
          bValue = new Date(bValue);
        } else {
          // case insensitive sorting
          aValue = aValue.toLowerCase();
          bValue = bValue.toLowerCase();
        }
      }

      if (aValue < bValue) {
        return this.currentSort.direction === DIRECTION.UP ? 1 : -1;
      }
      if (aValue > bValue) {
        return this.currentSort.direction === DIRECTION.UP ? -1 : 1;
      }
      return 0;
    });
  }
}
