























































































































import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import Card from '@/components/Card.vue';
import DatTitle from '@/components/DatTitle.vue';
import CardRow from '@/components/CardRow.vue';
import CursiveHeader from '@/components/CursiveHeader.vue';
import Divider from '@/components/Divider.vue';
import DatSubtitle from '@/components/DatSubtitle.vue';
import BlockHeader from '@/components/BlockHeader.vue';
import ImageTriangleWithText from '@/components/ImageTriangleWithText.vue';
import ImageWithText from '@/components/ImageWithText.vue';
import CardWithFlorals from '@/components/CardWithFlorals.vue';

@Component({
    components: {
        CardWithFlorals,
        ImageWithText,
        ImageTriangleWithText,
        BlockHeader,
        DatSubtitle,
        Divider,
        CursiveHeader,
        CardRow,
        DatTitle,
        Card,
    },
})
export default class Details extends Vue {
    public highlightRegistry: boolean = false;
    public highlightCovid: boolean = false;

    @Prop() public anchorToHighlight!: string;

    public mounted() {
      // watch isn't triggered when navigating to this page so need to call function on mount
      this.highlightAnchor(this.anchorToHighlight);
    }

    @Watch('anchorToHighlight')
    public highlightAnchor(anchor) {
        if (anchor === '#registry-question') {
            this.highlightRegistry = true;
            this.highlightCovid = false;
        } else if (anchor === '#covid-question') {
            this.highlightCovid = true;
            this.highlightRegistry = false;
        } else {
          this.highlightRegistry = false;
          this.highlightCovid = false;
        }
    }
}
