import Vue from 'vue';
import Router from 'vue-router';
import Details from './views/Details.vue';
import RSVP from '@/views/RSVP.vue';
import Accommodations from '@/views/Accommodations.vue';
import RSVPList from '@/views/RSVPList.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/index.html',
      component: Details,
      alias: '/',
    },
    {
      path: '/',
      name: 'Details',
      component: Details,
    },
    {
      path: '/rsvp',
      name: 'RSVP',
      component: RSVP,
    },
    {
      path: '/accommodations',
      name: 'Accommodations',
      component: Accommodations,
    },
    {
      path: '/rsvp-list',
      name: 'RSVP List',
      component: RSVPList,
    },
  ],
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash, offset: { x: 0, y: 100 },
      };
    }
    // on mobile, scroll to top on page change
    if (window.innerWidth <= 1040) {
      return { x: 0, y: 0 };
    }
    // if header is visible, scroll to the point where the header becomes sticky (accounting for responsiveness)
    if (window.innerWidth <= 1300) {
      return { x: 0, y: 252 };
    }
    return { x: 0, y: 352 };
  },
});
