







import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import {getNextId} from '@/helpers/DomHelpers';
@Component({
    components: {},
})
export default class InputTextArea extends Vue {
    public fieldText: string = '';

    @Prop() public value!: string;

    @Prop()
    public label!: string;
    private inputId: string = getNextId();

    public mounted() {
        this.fieldText = this.value;
    }

    @Watch('fieldText')
    public onPropertyChanged(value: string) {
        this.$emit('input', this.fieldText);
    }

    @Watch('value')
    public onValueChanged(value: string) {
        this.fieldText = value;
    }
}
